.btn-reset, .link-reset, .input-reset, .list-reset, .text-reset {
  margin: 0;
  padding: 0;
  border: 0;
}

*,
*::before,
*::after {
  -moz-box-sizing: inherit;
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

