/*! Basic */
.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px !important;
  width: 1px !important;
  padding: 0 !important;
  border: 0 !important;
  overflow: hidden;
}

/*! Reset classes */
.btn-reset {
  width: auto;
  margin: 0;
  padding: 0;
  background: transparent;
  outline: 0;
  color: inherit;
  font-family: inherit;
  font: inherit;
  cursor: pointer;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
}

.btn-reset::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.btn-reset:focus {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.btn-reset:focus-visible {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.btn-reset:focus:not(:focus-visible) {
  border-radius: 0;
  box-shadow: none;
}

.link-reset {
  outline: 0;
  color: inherit;
  text-decoration: none;
}

.link-reset:visited {
  text-decoration: none;
}

.link-reset:focus {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.link-reset:focus-visible {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.link-reset:focus:not(:focus-visible) {
  box-shadow: none;
  border-radius: 0;
}

.input-reset {
  outline: 0;
  -webkit-appearance: none;
}

.input-reset::-ms-clear {
  display: none;
}

.input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-decoration {
  -webkit-appearance: none;
  appearance: none;
}

.input-reset:focus {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.input-reset:focus-visible {
  box-shadow: 0 0 0 3px #8edc44;
  border-radius: 5px;
}

.input-reset:focus:not(:focus-visible) {
  box-shadow: none;
  border-radius: 0;
}

.list-reset {
  text-indent: 0;
  list-style: none;
  list-style-type: none;
}

.text-reset {
  font-size: inherit;
  font-weight: 400;
  font-style: normal;
}

