.main-btn {
  display: inline-block;
  padding: 17px 35px;
  border: 2px solid var(--color-green);
  border-radius: 8px;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  transition: border 0.3s ease, background-color 0.3s ease, box-shadow 0.3s ease, color 0.3s ease;
}

.main-btn--green {
  background-color: var(--color-green);
  box-shadow: 0px 10px 50px rgba(26, 188, 15, 0.65);
  color: var(--color-white);
}

@media (any-hover: hover) {
  .main-btn--green:hover {
    box-shadow: 5px 10px 50px 10px rgba(26, 188, 15, 0.65);
    transition: box-shadow 0.3s ease, color 0.3s ease;
  }
}

.main-btn--green:focus {
  box-shadow: 0 0 0 3px #ff5e5e;
}

.main-btn--green:focus:not(:focus-visible) {
  border-radius: 8px;
  box-shadow: 5px 10px 50px 10px rgba(26, 188, 15, 0.65);
}

.main-btn--transparent {
  color: var(--color-green);
  filter: drop-shadow(0px 10px 50px rgba(26, 188, 15, 0.65));
}

@media (any-hover: hover) {
  .main-btn--transparent:hover {
    background-color: var(--color-green);
    filter: drop-shadow(0 10px 50px 10px rgba(26, 188, 15, 0.65));
    color: var(--color-white);
  }
}

.main-btn--transparent:focus {
  box-shadow: 0 0 0 3px #ff5e5e;
}

.main-btn--transparent:focus:not(:focus-visible) {
  background-color: var(--color-green);
  filter: drop-shadow(0 10px 50px 10px rgba(26, 188, 15, 0.65));
  color: var(--color-white);
  border-radius: 8px;
}

