.pluses {
  padding: 120px 0;
  background-color: var(--color-dark-grey);
}

@media (max-width: 767.98px) {
  .pluses {
    padding-top: 240px;
  }
}

.pluses__heading {
  margin-bottom: 120px;
}

@media (max-width: 1215px) {
  .pluses__heading {
    margin-bottom: 90px;
  }
}

.pluses__list {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 75px;
}

@media (max-width: 1050px) {
  .pluses__list {
    justify-content: center;
  }
}

@media (max-width: 767.98px) {
  .pluses__list {
    flex-direction: column;
  }
}

.pluses__item {
  flex: 0 0 calc(33.33% - 33.33px);
}

.pluses__item:not(:last-child) {
  margin-right: 50px;
}

@media (max-width: 1215px) {
  .pluses__item {
    flex: 0 0 calc(33.33% - 20px);
  }
  .pluses__item:not(:last-child) {
    margin-right: 30px;
  }
}

@media (max-width: 1050px) {
  .pluses__item {
    flex: 0 0 calc(50% - 25px);
  }
  .pluses__item:not(:last-child) {
    margin-right: 0;
  }
  .pluses__item:not(:nth-child(2n - 2)) {
    margin-right: 50px;
  }
  .pluses__item:not(:nth-last-child(-n + 1)) {
    margin-bottom: 50px;
  }
  .pluses__item:last-child {
    margin-right: 0;
  }
}

@media (max-width: 767.98px) {
  .pluses__item:not(:last-child) {
    margin-right: 0;
  }
  .pluses__item:not(:nth-child(2n - 2)) {
    margin-right: 0;
  }
  .pluses__item:not(:nth-last-child(-n + 1)) {
    margin-bottom: 0;
  }
  .pluses__item:not(:last-child) {
    margin-bottom: 60px;
  }
}

.pluses__links {
  text-align: center;
}

.pluses__link {
  display: inline-block;
  max-width: 223px;
  padding: 18px 36px;
  background-color: var(--color-timber-green);
  border-radius: 8px;
  text-align: center;
  transition: background-color 0.3s ease;
}

.pluses__link:not(:last-child) {
  margin-right: 40px;
}

.pluses__link strong {
  font-weight: 700;
}

.pluses__link:focus {
  background-color: #1e5d67;
  box-shadow: 0 0 0 3px #46eb4f;
}

.pluses__link:focus:not(:focus-visible) {
  border-radius: 8px;
  background-color: #1e5d67;
  transition: background-color 0.3s ease;
}

@media (any-hover: hover) {
  .pluses__link:hover {
    background-color: #1e5d67;
    transition: background-color 0.3s ease;
  }
}

@media (max-width: 767.98px) {
  .pluses__link {
    display: block;
    max-width: 400px;
    margin-right: auto;
    margin-left: auto;
  }
  .pluses__link:not(:last-child) {
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 20px;
  }
}

.pluses-item {
  display: flex;
  align-items: center;
}

.pluses-item--tip::before {
  background: url(../img/pluses1.svg) center/contain no-repeat;
}

.pluses-item--analitic::before {
  background: url(../img/pluses2.svg) center/contain no-repeat;
}

.pluses-item--guest::before {
  background: url(../img/pluses3.svg) center/contain no-repeat;
}

.pluses-item::before {
  content: "";
  flex: 0 0 118px;
  display: inline-block;
  width: 118px;
  height: 118px;
  margin-right: 30px;
}

@media (max-width: 1215px) {
  .pluses-item::before {
    flex: 0 0 85px;
    width: 85px;
    height: 85px;
  }
}

@media (max-width: 767.98px) {
  .pluses-item::before {
    flex: 0 0 65px;
    width: 65px;
    height: 65px;
  }
}

.pluses-item__heading {
  margin-bottom: 15px;
}

@media (max-width: 767.98px) {
  .pluses-item__heading {
    margin-bottom: 10px;
  }
}

.simple {
  padding: 120px 0;
  overflow: hidden;
  background-color: var(--color-black);
}

.simple__heading {
  margin-bottom: 80px;
}

@media (max-width: 479.98px) {
  .simple__heading {
    margin-bottom: 60px;
  }
}

.simple__swiper {
  margin-bottom: 30px;
}

.simple__swiper--platform {
  margin-bottom: 0;
}

.simple__text {
  max-width: 400px;
  margin: 0 auto;
  color: var(--color-green);
  text-align: center;
}

.swiper {
  position: relative;
  z-index: 1;
  width: 100%;
}

@media (max-width: 767.98px) {
  .swiper {
    padding-bottom: 30px;
  }
}

.swiper__pagination {
  position: absolute;
  bottom: 0px;
  left: 50%;
  z-index: 2;
  display: none;
  transform: translateX(-50%);
}

.swiper__pagination .swiper-pagination-bullet {
  display: block;
  width: 30px;
  height: 2px;
  background-color: var(--color-white);
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.swiper__pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 15px;
}

.swiper__pagination .swiper-pagination-bullet-active {
  background-color: var(--color-green);
  transition: background-color 0.3s ease;
}

@media (max-width: 767.98px) {
  .swiper__pagination {
    display: flex;
  }
}

.swiper__wrapper {
  display: flex;
  width: 100%;
  height: 100%;
}

.swiper__slide {
  flex-shrink: 0;
}

@media (min-width: 769px) {
  .swiper__slide {
    position: relative;
    flex: 0 0 calc(33.33% - 13.33px);
  }
  .swiper__slide:not(:last-child) {
    margin-right: 20px;
  }
  .swiper__slide::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 10px;
    height: 10px;
    background-color: var(--color-green);
    border-radius: 50%;
    transform: translate(-100%, -35px);
  }
  .swiper__slide:last-child::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 10px;
    height: 10px;
    background-color: var(--color-green);
    border-radius: 50%;
    transform: translate(100%, -35px);
  }
}

.slide__image {
  width: 100%;
  height: 470px;
  margin-bottom: 15px;
  object-fit: contain;
}

.slide__text {
  max-width: 290px;
  margin: 0 auto;
  text-align: center;
}

@media (max-width: 769px) {
  .slide__text {
    font-size: 15px;
  }
}

.banner {
  padding: 80px 0;
  background: url(../img/banner-bg.jpg) center/cover no-repeat;
}

@media (max-width: 479.98px) {
  .banner {
    padding: 60px 0;
  }
}

.banner__text {
  margin-bottom: 15px;
}

.banner__heading {
  max-width: 790px;
  text-align: left;
}
